import myScrollX from '@/components/my-scrollX/my-scrollX.vue'
import RenDropdownFilter from '@/components/ren-dropdown-filter/ren-dropdown-filter.vue'
import MyList from '@/components/my-list/my-list.vue'
export default {
	components: {
		myScrollX,
		RenDropdownFilter,
		MyList,
	},
	data() {
		return {
			goodsName:'',
			scrollTopList:[],
			currentTopTab: 0,
			beforeColor: '#999999',
			afterColor: '#ffffff',
			filterData: [
				[{
					text: '全部收藏',
					value: 0
				},{
					text: '收藏升序',
					value: 2
				}, {
					text: '收藏降序',
					value: 1
				}],
				[{
					text: '全部特权',
					value: 3
				}, {
					text: '专属VIP',
					value: 1
				}, {
					text: '免费使用',
					value: 0
				}, {
					text: '消耗能量',
					value: 2
				}],
				[{
					text: '全部收藏',
					value: 0
				}, {
					text: '已收藏',
					value: 1
				}, {
					text: '未收藏',
					value: 2
				}],
				[{
					text: '全部类型',
					value: 0
				}, {
					text: '推荐类型',
					value: 1
				}]
			],
			defaultIndex: [0, 0, 0, 0],
			trainInfo:{},
			collectsList:[],
			page:1,
			contentText: {
				contentdown: '查看更多',
				contentrefresh: '加载中',
				contentnomore: '- 暂时没有新内容了呢 -'
			},
			status: 'loading',
			parameterInfo:{
				groupId: '', //组别id（选填）
				orderType:0, //排序类型1收藏逆序2收藏正序0默认时间倒序
				vipType:3, //vip类型1vip2消耗能量0免费3全
				attentionType:0,//收藏类型1已收藏2未收藏0全部
				recommendType:0, //推荐类型1推荐0全部
				collectsName: '',
			}, //初始化值
			code:'',
		};
	},
	onPullDownRefresh() {
		this.page = 1;
		this.trainInfo = {};
		this.collectsList = []
		uni.showLoading({
			title: '加载中'
		});
		this.getTrainList()
		uni.hideLoading();
		uni.stopPullDownRefresh()
	},
	onReachBottom(){
		if (this.code != '-116') {
			this.page = this.page + 1;
			this.getTrainList();
		}
	},
	onShow(){
		this.page = 1;
		this.trainInfo = {};
		this.collectsList = []
		this.getTrainList()
	},
	methods:{
		// 获取训练数据列表
		getTrainList(){
			if(uni.getStorageSync('userInfo')){
				let memberId = JSON.parse(uni.getStorageSync('userInfo')).id
				this.$Request.get('/appCollectsController.do?getCollectsList&type=1',{
					...this.parameterInfo,
					page:this.page,
					memberId:memberId
				}).then(res => {
					this.trainInfo = res.data
					res.data.groupList.unshift({
						id:'',
						groupName: '全部'
					})
					this.scrollTopList = res.data.groupList
					this.status = 'noMore'
					if(res.code == 0){
						this.collectsList =  [...this.collectsList, ...res.data.collectsList].map(item => {
							return {
								...item,
								studyDate: item.studyDate && item.studyDate.substring(0,10)
							}
						})
					}else if(res.code == '-118' || res.code == '-116'){
						this.status = 'noMore'
					}else{
						uni.showToast({
							title: res.info,
							icon: 'none'
						})
					}
				})
			}else{
				uni.showToast({
					title: '您尚未登录，正在跳往登录页面。。。',
					icon: 'none'
				})
				setTimeout(() => {
					uni.navigateTo({
						url:'/pages/loginAll/login'
					})
				}, 1000)
			}
		},
		tabChange(item, index){
			this.page = 1;
			this.trainInfo = {};
			this.collectsList = []
			this.currentTopTab = index
			this.parameterInfo.groupId = item.id
			this.getTrainList()
		},
		onSelected(res) {
			this.page = 1;
			this.trainInfo = {};
			this.collectsList = []
			this.parameterInfo.orderType = res[0][0].value
			this.parameterInfo.vipType = res[1][0].value
			this.parameterInfo.attentionType = res[2][0].value
			this.parameterInfo.recommendType = res[3][0].value
			this.getTrainList()
		},
		gotoListDetail(item){
			uni.navigateTo({
				url: `/pages/train/imageMemory/numEleEntry?id=${item.id}`
			})
		},
		// 收藏
		clickAttention(item, index){
			let memberId = JSON.parse(uni.getStorageSync('userInfo')).id
			let collectsId = item.id
			this.$Request.get(`/appAttentionController.do?takeCollectsAttention&memberId=${memberId}&collectsId=${collectsId}`)
			.then(res => {
				if(res.code == 0){
					this.collectsList[index].attentionNum = item.attentionType == 1 ? item.attentionNum - 1 : item.attentionNum + 1
					this.collectsList[index].attentionType = item.attentionType == 1 ? 0 : 1
				}else{
					uni.showToast({
						title: res.info,
						icon: 'none'
					})
				}
			})
		},
		bindNameInput(e){
			this.parameterInfo.collectsName = e.target.value
			this.page = 1;
			this.trainInfo = {};
			this.collectsList = []
			this.getTrainList()
		},
		clickSearch(){
			this.page = 1;
			this.trainInfo = {};
			this.collectsList = []
			this.getTrainList()
		},
	}
}
